import React, { Component, Fragment } from 'react';

export default class Task extends Component {
	constructor(props) {
		super(props)
		this.state = {
			isEdit: false
		}

		this.delRecord = this.delRecord.bind(this)
		this.editRecord = this.editRecord.bind(this)
		this.updateRecord = this.updateRecord.bind(this)
	}

	delRecord(e) {
	    const id = e.target.getAttribute('datakey')
	    
	    fetch("http://localhost:4000/tasks/"+id, {
	      method: "DELETE",
	      headers: {
	        'Content-Type': 'Application/json'
	      }
	    })
	    .then(() => {
	      fetch("http://localhost:4000/tasks")
	      .then(res => res.json())
	      .then(tasks => this.props.onDel(tasks))
	      .catch(err => console.log(err))
	    })
	    .catch(err => console.log(err))
	}

	editRecord() {
		this.setState({isEdit:true})
	}

	updateRecord(e) {
		e.preventDefault()
	    const id = e.target.id.value

	    fetch("http://localhost:4000/tasks/"+id, {
		    method: "PUT",
		    headers: {
		        'Content-Type': 'Application/json'
		    },
			body: JSON.stringify({
				"title": e.target.task.value,
				"isCompleted": "false"
			})
	    })
	    .then(res => res.json())
	    .then(task => {
	    	this.props.onUpdate(task)
	    	this.setState({isEdit:false})
	    })
	    .catch(err => console.log(err))


	}

	render() {
		const {id, title, isCompleted} = this.props
		const task = (this.state.isEdit === true) ? 
			<form onSubmit={this.updateRecord}>
			  <input type="hidden" name="id" id="id" defaultValue={id} />
			  <div className="row">
			  	<div className="col-md-10">
				  <div className="form-group">
					<input type="text" className="form-control" name="task" id="task" defaultValue={title} />
				  </div>
			  	</div>
			  	<div className="col-md-2">
				  <button type="submit" className="btn btn-primary">Update</button>
			  	</div>
			  </div>
			</form> 
			:
			<li className="list-group-item" key={id}>{title} - {isCompleted} -  
			<button className="btn btn-primary btn-sm" onClick={this.editRecord} datakey={id} >Edit</button> - 
			<button className="btn btn-danger btn-sm" onClick={this.delRecord} datakey={id} >Delete</button></li>
		return (
			<Fragment>{task}</Fragment>
		);
	}
}



