import React, { Component } from 'react';
import logo from '../logo.svg';
import '../App.css';

import AddForm from './AddForm'
import Task from './Task'

export default class Tasks extends Component {
  constructor(props){
    super(props)
    this.state = {
      tasks:[]
    }
    this.addRecord = this.addRecord.bind(this)
    this.delRecord = this.delRecord.bind(this)
    this.getRecord = this.getRecord.bind(this)
  }

  componentDidMount() {
    fetch("http://localhost:4000/tasks")
    .then(res => res.json())
    .then(tasks => this.setState({tasks}))
    .catch(err => console.log(err))
  }

  getRecord(e) {
    const tasks = this.state.tasks.map(task => {
      if(e.id === task.id) {
        return e
      }
      return task
    })
    this.setState({tasks})
  }

  addRecord(data) {
    // console.log(data)
    const tasks = [...this.state.tasks, data];
    this.setState({tasks})

  }

  delRecord(e) {
    this.setState({tasks:e})
  }

  render() {
    const tasks = this.state.tasks.map(task => <Task key={task.id} {...task} onDel={this.delRecord} onUpdate={this.getRecord} />)
    return (
      <div className="App">
        <div className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
        </div>
          <div className="container">
            <div className="row">
              <div className="col-md-6 col-md-offset-3">
                <AddForm addRec={this.addRecord} />
                <ul className="list-group">
                  <li className="list-group-item active">Todo Tasks List</li>
                  {tasks}
                </ul>
              </div>
            </div>
          </div>
        
      </div>
    );
  }
}




