import React, { Component } from 'react';

export default class AddForm extends Component {
	constructor(){
	    super()
	    this.addRecord = this.addRecord.bind(this)
	}
	addRecord(e) {
		e.preventDefault()
		fetch("http://localhost:4000/tasks", {
			method: "POST",
			headers: {
				'Content-Type': 'Application/json'
			},
			body: JSON.stringify({
				"id": Math.floor((Math.random() + 1) * 0x100000),
				"title": e.target.task.value,
				"isCompleted": "false"
			})
		})
		.then(res => res.json())
		.then(result => this.props.addRec(result))
		.catch(err => console.log(err))

		e.target.reset()
	}
	render() {
		return (
			<div>
				<h2>Create Todo List</h2>
				<form onSubmit={this.addRecord}>
				  <div className="row">
				  	<div className="col-md-10">
					  <div className="form-group">
						<input type="text" className="form-control" name="task" id="task" placeholder="write your task" />
					  </div>
				  	</div>
				  	<div className="col-md-2">
					  <button type="submit" className="btn btn-primary">Add Task</button>
				  	</div>
				  </div>
				</form>
			</div>
		);
	}
}



